body {
    font-family: 'liquid_crystalregular', sans-serif;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
.clock {
    height: 100px;
    width: 70%;
    line-height: 100px;  
    margin: 150px auto 0;
    padding: 0 50px;
    background: #222;
    color: #eee;
    text-align: center;
    border-radius: 15px;
    box-shadow: 0 0 7px #222;
    text-shadow: 0 0 3px #fff;
  }  

.App .MessageList {
  box-sizing: border-box;
  flex: 1 1;
}

.App {
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 500px;
  width: 100%;
  margin: .5em;
  font-size: 13px;
  display: flex;
  flex-direction: column;
}


.App .MessageForm {
  background: #eee;
  border-top: 1px solid #ccc;
  border-radius: 0 0 3px 3px;
  box-sizing: border-box;
  flex: 0 0 30px;
}



.MessageForm {
    display: flex;
    background: #ccc;
  }
  
  .MessageForm .input-container {
    flex: 1 1;
    margin: 1px;
  }
  
  .MessageForm .button-container {
    flex: 0 0 6em;
    margin: 1px 1px 1px 0;
  }
  
  .MessageForm input, .MessageForm button {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    height: 100%;
    outline: none;
    width: 100%;
  }
  
  .MessageForm input {
    padding: 0 0 0 4px;
  }
.Message {
    line-height: 1.5em;
  }
  
  .Message.log {
    color: #bbb;
  }
  
  .Message .author {
    font-weight: bold;
    color: #888;
    margin: 0 5px 0 0;
  }
  
  .Message.me .author {
    color: #b6191e;
  }
.MessageList {
    overflow-y: auto;
    padding: 4px;
  }
#offTrack{
    background-color: rgb(255,144,82)
}
#greatPoint{
    background-color: rgb(0,166,255)
}
#raiseHand{
    background-color: rgb(184,151,126)
}

.Chat .MessageList {
  box-sizing: border-box;
  flex: 1 1;
}

.Chat {
  border: 1px solid #ccc;
