  margin: .5em;
  font-size: 13px;
  display: flex;
  flex-direction: column;
}


.Chat .MessageForm {
  background: #eee;
  border-top: 1px solid #ccc;
  border-radius: 0 0 3px 3px;
  box-sizing: border-box;
  flex: 0 0 30px;
}


.attendees{
    border: 1px solid #ccc;
